import React, { useState } from "react"

import { makeStyles } from "@material-ui/core/styles"
import Box from "@material-ui/core/Box"
import Collapse from "@material-ui/core/Collapse"
import IconButton from "@material-ui/core/IconButton"
import Table from "@material-ui/core/Table"
import TableBody from "@material-ui/core/TableBody"
import TableCell from "@material-ui/core/TableCell"
import TableContainer from "@material-ui/core/TableContainer"
import TableHead from "@material-ui/core/TableHead"
import TableRow from "@material-ui/core/TableRow"
import Typography from "@material-ui/core/Typography"
import Paper from "@material-ui/core/Paper"
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown"
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp"

const DRSTableRow = ({ drs, drsInfo }) => {

    const [open, setOpen] = React.useState(false);
    const rowInfo = drsInfo[drs]

    const {
        total_de_leitos,
      ocupacao_de_leitos,
      internacoes,
      obitos,
      casos,
      fase,
    } = rowInfo
    
    const leitos_por_100k = total_de_leitos['leitos_por_100k'] !== 'NaN' ? Math.round(total_de_leitos['leitos_por_100k']*10)/10 : '-'

    const ocupacao_uti = ocupacao_de_leitos['ocupacao_leitos_uti'] !== 'NaN' ? `${Math.round(ocupacao_de_leitos['ocupacao_leitos_uti']*10)/10}%` : '-'
    
    const var_casos = casos['var'] !== 'NaN' ? `${Math.round((casos['var']-1)*100)}%` : '-'
    
    const var_obitos = obitos['var'] !== 'NaN' ? `${Math.round((obitos['var']-1)*100)}%` : '-'
    
    const var_internacoes = internacoes['var'] !== 'NaN' ? `${Math.round((internacoes['var']-1)*100)}%` : '-'
    
    const fase_final = fase['fase_final']

    return (
        <>
        <TableRow>

        <TableCell>
          <IconButton size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {drs}
        </TableCell>
        <TableCell align="right">{leitos_por_100k}</TableCell>
        <TableCell align="right">{ocupacao_uti}</TableCell>
        <TableCell align="right">{var_internacoes}</TableCell>
        <TableCell align="right">{var_casos}</TableCell>
        <TableCell align="right">{var_obitos}</TableCell>
        <TableCell align="right">{fase_final}</TableCell>
        </TableRow>
        </>
    )
  }

function DRSTable({ infoData, date }) {

  const drsList = [
    "Estado de São Paulo",
    "Município de São Paulo",
    "Grande SP Leste",
    "Grande SP Norte",
    "Grande SP Oeste",
    "Grande SP Sudeste",
    "Grande SP Sudoeste",
    "DRS 02 Araçatuba",
    "DRS 03 Araraquara",
    "DRS 04 Baixada Santista",
    "DRS 05 Barretos",
    "DRS 06 Bauru",
    "DRS 07 Campinas",
    "DRS 08 Franca",
    "DRS 09 Marília",
    "DRS 10 Piracicaba",
    "DRS 11 Presidente Prudente",
    "DRS 12 Registro",
    "DRS 13 Ribeirão Preto",
    "DRS 14 São João da Boa Vista",
    "DRS 15 São José do Rio Preto",
    "DRS 16 Sorocaba",
    "DRS 17 Taubaté",
  ]

  const drsInfo = infoData[date]

  console.log(drsInfo)
  return (
      <div>
        <TableContainer component={Paper} className="center" style={{ margin: "20px auto", padding: "10px" }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell />
                <TableCell>Região</TableCell>
                <TableCell align="right">Leitos</TableCell>
                <TableCell align="right">Ocupação</TableCell>
                <TableCell align="right">Internações</TableCell>
                <TableCell align="right">Casos</TableCell>
                <TableCell align="right">Óbitos</TableCell>
                <TableCell align="right">Fase</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
                {drsList.map(drs => (
                <DRSTableRow key={drs} drs={drs} drsInfo={drsInfo} />
                ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
  )
}

export default DRSTable
